import QRCode from "qrcode";

export const generateQRCode = async ({crew, district, location, project}) => {
  const value = `;${district}///${location}/${crew}/${project}//;`;

  const url = await QRCode.toDataURL(value, {
    color: {light: "#0000"},
    scale: 15,
  });

  return {url, value};
};
