import {createMuiTheme, CssBaseline, MuiThemeProvider} from "@material-ui/core";
import {BrowserRouter, HashRouter, Route} from "react-router-dom";
import qs from "qs";
import React from "react";

import {Generator} from "routes/generator";
import {isOfflineMode} from "utils/is-offline-mode";
import {setToken} from "utils/set-token";

import {AuthenticatedRoute} from "./authenticated-route";

const theme = createMuiTheme({});

function App() {
  const Router = isOfflineMode() ? HashRouter : BrowserRouter;

  return (
    <MuiThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        {!isOfflineMode() && (
          <Route
            exact
            path={"/login/callback"}
            render={({history, location}) => {
              const params = qs.parse(location.search.replace(/^\?/, ""));
              setToken(params.id_token);
              history.push("/");
            }}
          />
        )}
        <AuthenticatedRoute exact path={"/"} component={Generator} />
      </Router>
    </MuiThemeProvider>
  );
}

export default App;
