/* eslint-disable react-hooks/exhaustive-deps */

import {Typography} from "@material-ui/core";
import find from "lodash/find";
import Grid from "@material-ui/core/Grid";
import isEmpty from "lodash/isEmpty";
import React, {useEffect} from "react";

import {useMergeState} from "hooks/use-merge-state";

import {fetchLookups} from "./fetch-lookups";
import {generateQRCode} from "./generate-qrcode";
import {mapOptions} from "./map-options";
import {Select} from "./select";
import {useStyles} from "./styles";
import logo from "./logo.png";

export const Generator = () => {
  const classes = useStyles();
  const {state, mergeState} = useMergeState();

  const {
    code,
    crew,
    crews = [],
    customer,
    district,
    districts = [],
    location,
    locations = [],
    project,
    projects = [],
  } = state;

  useEffect(() => {
    (async () =>
      mergeState({
        code: await generateQRCode({crew, district, location, project}),
      }))();
  }, [crew, district, location, project]);

  useEffect(() => {
    (async () => mergeState(await fetchLookups()))();
  }, []);

  const handleChange = key => e => {
    mergeState({[key]: e.target.value});

    if (key === "project") {
      const project = find(projects, {name: e.target.value});

      if (project) {
        mergeState({customer: project.customerName});
      }
    }
  };

  const isValid =
    !isEmpty(code) &&
    !isEmpty(crew) &&
    !isEmpty(district) &&
    !isEmpty(location) &&
    !isEmpty(customer) &&
    !isEmpty(project);

  return (
    <>
      <div className={`no-print ${classes.center}`}>
        <Select
          className={classes.select}
          label={"District"}
          onChange={handleChange("district")}
          options={mapOptions(districts)}
          value={district}
        />
        <Select
          className={classes.select}
          label={"Crew"}
          onChange={handleChange("crew")}
          options={mapOptions(crews)}
          value={crew}
        />
        <Select
          className={classes.select}
          label={"Location"}
          onChange={handleChange("location")}
          options={mapOptions(locations)}
          value={location}
        />
        {projects.length === 0 && (
          <Select
            className={classes.select}
            label={"Customer"}
            onChange={handleChange("customer")}
            options={mapOptions(projects)}
            value={customer}
          />
        )}
        <Select
          className={classes.select}
          label={"Project"}
          onChange={handleChange("project")}
          options={mapOptions(projects)}
          value={project}
        />
      </div>
      {isValid && (
        <div className={classes.page}>
          <Typography variant={"h3"}>Calfrac Punch In</Typography>
          <Grid container className={classes.table}>
            <Grid item xs={9}>
              {[
                {label: "District", value: district},
                {label: "Job Crew", value: crew},
                {label: "Location", value: location},
                {label: "Customer", value: customer},
                {label: "Project", value: project},
              ].map(item => {
                return (
                  <Grid container key={item.label}>
                    <Grid item xs={3}>
                      <Typography className={classes.bold}>
                        {item.label}
                      </Typography>
                    </Grid>
                    <Grid item xs={9}>
                      <Typography>{item.value}</Typography>
                    </Grid>
                  </Grid>
                );
              })}
            </Grid>
            <Grid item xs={3}>
              <img src={logo} className={classes.logo} alt={"calfrac logo"} />
            </Grid>
          </Grid>
          <img className={classes.qrcode} src={code.url} alt={code.value} />
          <br />
          <Typography variant={"h6"} className={"no-print"}>
            {code.value}
          </Typography>
        </div>
      )}
    </>
  );
};
