import axios from "axios";
import get from "lodash/get";

import {getToken} from "utils/get-token";
import {isOfflineMode} from "utils/is-offline-mode";

export const fetchLookups = async () => {
  if (isOfflineMode()) {
    return;
  }

  const basePath = process.env.REACT_APP_API_PATH;

  axios.defaults.headers.common["Authorization"] = `Bearer ${getToken()}`;

  try {
    const [crews, districts, locations, projects] = await Promise.all([
      axios.get(`${basePath}/v1/crews`),
      axios.get(`${basePath}/v1/districts`),
      axios.get(`${basePath}/v1/locations`),
      axios.get(`${basePath}/v1/projects`),
    ]);

    return {
      crews: crews.data,
      districts: districts.data,
      locations: locations.data,
      projects: projects.data,
    };
  } catch (e) {
    if ([401, 403].includes(get(e, "response.status"))) {
      return (window.location.href = `${basePath}/login/azure`);
    }

    console.log("Failed to fetch lookups");
    console.log(e);
  }
};
