import {TextField} from "@material-ui/core";
import React from "react";

export const Select = props => {
  const {className, label, onChange, options, value} = props;

  if (options.length > 0) {
    return (
      <TextField
        className={className}
        select
        label={label}
        value={value}
        onChange={onChange}
        SelectProps={{native: true}}
        margin="normal"
      >
        <option value={"-1"}>{`Select a ${label.toLowerCase()}`}</option>
        {options.map(option => (
          <option key={option} value={option}>
            {option}
          </option>
        ))}
      </TextField>
    );
  }

  return (
    <TextField
      className={className}
      label={label}
      value={value || ""}
      onChange={onChange}
      margin="normal"
    />
  );
};
